*, *:after, *:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  height: 100%; }

#page-wrap {
  padding: 0; }

a {
  color: #4e4a46;
  text-decoration: none; }
  a:hover, a:focus {
    color: #c94e50; }

main {
  height: 100%;
  padding: 3em 2em;
  text-align: center;
  overflow: auto; }

h1 {
  font-weight: 800;
  font-size: 3.75em; }

.description {
  max-width: 20em;
  margin: 1.2em auto 1em;
  line-height: 1.3em;
  font-weight: 400; }

.demo-buttons {
  font-size: 1em;
  max-width: 1200px;
  margin: 2em auto 3em;
  padding: 0 10em; }
  .demo-buttons a {
    display: inline-block;
    margin: 0.75em;
    padding: 1.35em 1.1em;
    width: 15em;
    background: #fffce1;
    font-weight: 800;
    border-top-left-radius: 20px 50px;
    border-top-right-radius: 20px 50px;
    border-bottom-right-radius: 20px 50px;
    border-bottom-left-radius: 20px 50px;
    cursor: pointer; }
    .demo-buttons a.current-demo {
      background: #c94e50;
      color: #fffce1; }

.side-button {
  display: inline-block;
  width: 5em;
  height: 2.5em;
  line-height: 2.5em;
  cursor: pointer;
  background: #fffce1;
  font-size: 0.8em;
  font-weight: 800; }
  .side-button.left {
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px; }
  .side-button.right {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px; }
  .side-button.active {
    background: #c94e50;
    color: #fffce1; }

@media screen and (max-width: 40em) {
  main {
    font-size: 80%; }
  h1 {
    padding-top: 1em;
    font-size: 2.5em; }
  .demo-buttons {
    max-width: 900px;
    padding: 0 2em; } }

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px; }

.right .bm-burger-button {
  left: initial;
  right: 36px; }

.bm-burger-bars {
  background: #dedede;
  box-shadow: -1px -1px 1px 0 #1B1C1D; }

.bm-morph-shape {
  fill: #1B1C1D; }

.bm-menu {
  background: #1B1C1D; }
  .bm-menu a {
    color: #b8b7ad; }
    .bm-menu a:hover, .bm-menu a:focus {
      color: #c94e50; }

.bm-item-list a {
  padding: 0.8em; }
  .bm-item-list a span {
    margin-left: 10px;
    font-weight: 700; }

#slide .bm-cross, #stack .bm-cross, #reveal .bm-cross {
  background: #bdc3c7; }

#slide .bm-menu, #stack .bm-menu, #reveal .bm-menu {
  padding: 2.5em 1.5em 0;
  font-size: 1.15em; }

#bubble .bm-cross, #push .bm-cross {
  background: #999; }

#bubble .bm-menu, #push .bm-menu {
  padding: 3.4em 1em 0; }

#bubble a, #push a {
  padding: 1em; }

#bubble i, #push i {
  font-size: 1.7em;
  vertical-align: middle;
  color: #282a35; }

#elastic .bm-cross {
  background: #888; }

#elastic .bm-menu {
  padding: 2em 1em;
  font-size: 1.15em; }

#elastic i {
  opacity: 0.5; }

#scaleDown .bm-cross, #scaleRotate .bm-cross, #pushRotate .bm-cross, #fallDown .bm-cross {
  background: #888; }

#scaleDown h2, #scaleRotate h2, #pushRotate h2, #fallDown h2 {
  margin: 0 auto;
  padding: 2em 1em;
  color: rgba(0, 0, 0, 0.4);
  box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2); }
  #scaleDown h2 i, #scaleRotate h2 i, #pushRotate h2 i, #fallDown h2 i {
    margin-left: 0.2em; }
  #scaleDown h2 span, #scaleRotate h2 span, #pushRotate h2 span, #fallDown h2 span {
    font-size: 1.6em;
    font-weight: 700; }

#scaleDown a, #scaleRotate a, #pushRotate a, #fallDown a {
  padding: 1em;
  transition: background 0.3s, box-shadow 0.3s;
  box-shadow: inset 0 -1px rgba(0, 0, 0, 0.2); }
  #scaleDown a span, #scaleRotate a span, #pushRotate a span, #fallDown a span {
    letter-spacing: 1px;
    font-weight: 400; }
  #scaleDown a:hover, #scaleDown a:focus, #scaleRotate a:hover, #scaleRotate a:focus, #pushRotate a:hover, #pushRotate a:focus, #fallDown a:hover, #fallDown a:focus {
    background: rgba(0, 0, 0, 0.2);
    box-shadow: inset 0 -1px rgba(0, 0, 0, 0);
    color: #b8b7ad; }
